@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap');

:root {
  --primary: #fbcfe8;  /* light pink */
  --secondary: #d8b4fe; /* lilac */
  --text-dark: #374151;
  --bg: #fff0f6;
}

body {
  margin: 0;
  background-color: var(--bg);
  color: var(--text-dark);
  font-family: 'Poppins', sans-serif;
}

.App {
  max-width: 900px;
  margin: auto;
  padding: 2rem;
}

.hero {
  text-align: center;
  padding: 3rem 2rem;
  background: linear-gradient(to right, #fbcfe8, #d8b4fe);
  border-radius: 1.5rem;
  margin-bottom: 2rem;
  color: #111827;
}

.hero h1 {
  font-size: 2.3rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.hero p {
  font-size: 1.1rem;
  margin-bottom: 1rem;
}

.resume-btn {
  background-color: var(--secondary);
  color: white;
  padding: 0.6rem 1.2rem;
  text-decoration: none;
  border-radius: 999px;
  font-weight: bold;
  display: inline-block;
  transition: all 0.3s ease;
}

.resume-btn:hover {
  background-color: #c084fc;
}

section {
  margin-bottom: 3rem;
}

h2 {
  font-size: 1.7rem;
  margin-bottom: 1rem;
  color: #1f2937;
}

.quick-bio ul {
  list-style: none;
  padding-left: 0;
  line-height: 1.8;
  font-size: 1rem;
}

.project-grid {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}

.project-card {
  padding: 1rem;
  background-color: white;
  border: 1px solid #e5e7eb;
  border-radius: 1rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
}

.contact a {
  color: #9333ea;
  text-decoration: none;
}

.contact a:hover {
  text-decoration: underline;
}
